@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-flow: row;
  .stable{
    background-color:#93cfe9;
    width:300px;
    position: fixed;
    height: 100%;    
    border-style: outset;
    border-width: 5px;
  }
  .change{
    background-image: url(../assets/back1.jpg);
    background-size:cover;
    flex:1;
  }
}

.AppMobile {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-flow: row;
  .stableMob{
    background-color:#708090;
    width:80px;
    position: fixed;
    height: 100%;
    .name {
      font-family: 'Alata';
      font-size: 15px;
      margin: 2px 0;
    }
    div {
      color: white;
      font-size: 30px;
      margin: 20px auto;
      a {
        text-decoration:none;
        color: white;
      }
    }    
  }
  .changeMob{
    flex:1;
    background-color: #93cfe9;
  }
}

.react {
  color:#ff9000;
  font-weight: bold;
  margin-top: 66px;
  margin-bottom: 9px;
}
